.header {
  position: absolute;
  z-index: 3;
  top: 0;
  left: 0;
  width: 100%;
  padding: 2.5rem 0 0 0;
}
.header-top {
  &__inner {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 2rem;
  }
  &__right {
    flex-grow: 1;
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 2rem;
  }
  &__logo {
    width: 250px;
    display: block;
    img {
      width: 100%;
    }
  }
  &__group {
    display: flex;
    align-items: center;
    gap: 2rem;
  }
  ul {
    display: inline-grid;
    grid-template-columns: 100%;
    gap: 0.1875rem;
    li {
      position: relative;
      font-size: 0.875rem;
      line-height: 130%;
      padding-left: 0.625rem;
    }
    li:after {
      content: '';
      position: absolute;
      top: 0.375rem;
      left: 0;
      width: 0.3125rem;
      height: 0.3125rem;
      background: #ffffff;
      border-radius: 50%;
    }
  }
  &__items {
    display: inline-grid;
    grid-template-columns: 100%;
    gap: 0.5rem;
  }
  &__item {
    display: flex;
    align-items: center;
    i {
      flex-shrink: 0;
      width: 16px;
      display: flex;
      justify-content: center;
      align-items: center;
      margin-right: 0.75rem;
    }
    span {
      font-size: 14px;
      line-height: 130%;
    }
  }
  &__socials {
    display: flex;
    align-items: center;
    gap: 1.5rem;
  }
  &__social {
    position: relative;
    width: 2.5rem;
    height: 2.5rem;
    display: flex;
    justify-content: center;
    align-items: center;
    background: #077163;
    border-radius: 50%;
    overflow: hidden;
    &:hover {
      background: #39C2B0;
    }
    &:active {
      background: #00352E;
    }
    img {
      position: relative;
      z-index: 1;
      max-width: 1.5rem;
    }
    span {
      position: absolute;
      top: 0;
      left: 0;
    }
  }
  &__phone {
    font-family: Sansation;
    font-size: 24px;
    line-height: 130%;
    font-weight: 700;
    &:hover {
      color: #077163;
    }
  }
  &__btns {
    flex-shrink: 0;
    display: flex;
    flex-direction: column;
    gap: 0.75rem;
  }
  &__btn {
    justify-content: flex-end;
    background: transparent;
    &:hover {
      span {
        color: #077163;
        border-color: #077163;
      }
    }
    span {
      position: relative;
      font-size: 1rem;
      line-height: 130%;
      color: #ffffff;
      border-bottom: 0.0625rem dashed #ffffff;
      padding: 0 0 0.1875rem 0;
      overflow: unset;
    }
    span:after {
      content: '';
      position: absolute;
      top: 50%;
      left: -1.25rem;
      transform: translate(0, -50%);
      width: 0.5rem;
      height: 0.5rem;
      background: #ffffff;
      border-radius: 50%;
    }
    span:before {
      content: '';
      position: absolute;
      top: 50%;
      left: -1.375rem;
      transform: translate(0, -50%);
      width: 0.75rem;
      height: 0.75rem;
      background: #17BDA6;
      filter: blur(2.8499999046325684px);
    }
  }
  .menu {
    display: none;
  }
}
.header-bottom {
  padding-top: 2rem;
  .menu {
    display: grid;
    grid-template-columns: repeat(6, calc(100% / 6));
    background: linear-gradient(0deg, rgba(0, 53, 46, 0.87) 0%, rgba(0, 53, 46, 0.87) 100%);
    border-radius: 0.25rem;
  }
  .menu-item {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    &:after {
      content: '';
      position: absolute;
      top: 50%;
      left: unset;
      right: 0;
      transform: translate(0, -50%);
      width: 0.0625rem;
      height: 2.5rem;
      background: #9D9D9D;
      opacity: 1;
    }
  }
  .menu-item:last-child {
    &:after {
      display: none;
    }
  }
  .menu-item a {
    display: flex;
    align-items: center;
    font-size: 1.125rem;
    line-height: 130%;
    font-weight: 700;
    text-transform: uppercase;
    border-left: unset;
    padding: 1.5rem 2rem;
    i {
      flex-shrink: 0;
      width: 0.625rem;
      display: flex;
      align-items: center;
      margin-left: 0.5rem;
    }
    i svg {
      width: 100%;
      height: auto;
    }
  }
  .sub-menu {
    top: 100%;
    left: 0 !important;
    right: unset !important;
    background: linear-gradient(0deg, rgba(0, 53, 46, 0.87) 0%, rgba(0, 53, 46, 0.87) 100%) !important;
    padding: 0 !important;
    .menu-item:after {
      display: none;
    }
  }
}
.burger {
  position: relative;
  z-index: 12;
  width: 2rem;
  height: 1.25rem;
  cursor: pointer;
  display: none;
  span {
    position: absolute;
    left: 0;
    width: 100%;
    height: 0.1875rem;
    background: #ffffff;
    border-radius: 0.1875rem;
    transition: all 0.5s;
  }
  span:nth-child(1) {
    top: 0;
  }
  span:nth-child(2) {
    top: 50%;
    transform: translate(0, -50%);
  }
  span:nth-child(3) {
    bottom: 0;
  }
  &--active {
    span:nth-child(1) {
      top: 0.5rem;
      transform: rotate(-45deg);
    }
    span:nth-child(2) {
      display: none;
    }
    span:nth-child(3) {
      bottom: 0.5rem;
      transform: rotate(45deg);
    }
  }
}

@media screen and (max-width: 1399px) {
  .header-top {
    &__right {
      gap: 1.5rem;
    }
    ul {
      display: none;
    }
    &__socials {
      gap: 1rem;
    }
  }
  .header-bottom {
    .menu-item a {
      font-size: 1rem;
      padding: 1rem 1.5rem;
    }
  }
}

@media screen and (max-width: 1180px) {
  .header-top {
    &__logo {
      width: 13.75rem;
    }
  }
}

@media screen and (max-width: 1023px) {
  .header {
    padding-top: 1.5rem;
  }
  .header-top {
    &__right {
      position: fixed;
      z-index: 10;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background: linear-gradient(0deg, rgba(0, 53, 46, 1) 0%, rgba(0, 53, 46, 1) 100%);
      flex-direction: column;
      justify-content: start;
      padding: 6.25rem 2rem 5rem;
      opacity: 0;
      pointer-events: none;
      transition: all 0.3s;
      overflow-y: auto;
      &--active {
        opacity: 1;
        pointer-events: all;
      }
    }
    &__item {
      justify-content: center;
    }
    &__socials {
      order: 1;
    }
    &__btn {
      display: none !important;
    }
    .menu {
      order: -1;
      display: flex;
      flex-direction: column;
      gap: 1.5rem;
    }
    .menu-item {
      text-align: center;
      padding-left: 0;
      &:after,
      &:before {
        display: none;
      }
      &:hover {
        .menu-item__link {
          color: #077163;
          svg path {
            fill: #077163;
          }
        }
      }
    }
    .menu-item a {
      border-left: unset;
      padding: 0;
      i {
        display: none;
      }
    }
    .menu .sub-menu {
      display: none;
    }
  }
  .header-bottom {
    display: none;
  }
  .burger {
    display: block;
  }
}