.btn-green {
  background: linear-gradient(90deg, #07AB94 0.05%, #077163 214.13%);
  backdrop-filter: blur(0.125rem);
  border-radius: 0.4375rem;
  color: #FFF;
  &:hover {
    background: linear-gradient(90deg, #0BDCBF 6.37%, #39C2B0 109.51%);
  }
  &:active {
    background: linear-gradient(270deg, #00352E 0%, #01C0A7 189.72%);
  }
  span {
    font-size: 1.125rem;
    line-height: 120%;
    padding: 1.125rem;
  }
}
h2 {
  font-size: 2.25rem;
  line-height: 130%;
  margin: 0;
  strong {
    font-size: 3.5rem;
  }
}
.form-group {
  border: none;
  padding: 0;
  margin: 0;
  input {
    height: 3.5625rem !important;
    background: rgb(237, 236, 236) !important;
    border-radius: 0.375rem !important;
    padding: 0 1.25rem !important;
    font-size: 1rem !important;
    line-height: 130%;
    color: #000000 !important;
    &::placeholder {
      color: rgb(137, 132, 132);
    }
  }
}
.agreement-check {
  input:checked ~ label {
    &:after {
      background-image: url("../img/check.svg");
    }
  }
  label {
    font-size: 0.875rem;
    padding-left: 2.5rem;
    &:after {
      content: '';
      position: absolute;
      top: 50%;
      left: 0;
      transform: translate(0, -50%);
      width: 1.875rem;
      height: 1.875rem;
      background-repeat: no-repeat;
      background-position: center;
      background-size: auto;
      border: 0.0625rem solid rgb(23, 189, 166);
      border-radius: 0.5625rem;
    }
  }
}
#modal-not-agreement {
  color: #000000;
}
.button {
  display: flex;
  justify-content: center;
}
.navigation {
  display: inline-grid;
  grid-template-columns: 100%;
  gap: 1.5rem;
  padding: 3.125rem 0;
  color: #000;
  a {
    font-size: 1.5rem;
    line-height: 130%;
    &:hover {
      opacity: 0.7;
    }
  }
}

@media screen and (max-width: 1399px) {
  h2 {
    font-size: 2rem;
    strong {
      font-size: 3rem;
    }
  }
}

@media screen and (max-width: 1023px) {
  h2 {
    font-size: 1.75rem;
    strong {
      font-size: 2.25rem;
    }
  }
}