html {
  font-size: max(calc(16px * var(--coef_h)), 15px);
}
body {
  font-family: Mulish, sans-serif;
  background: #00221D;
  color: #ffffff;
}
.fixed-body {
  overflow: hidden;
}
.wrapper {
  position: relative;
  overflow: hidden;
}
.container-fluid {
  max-width: 1700px;
}
h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: Sansation;
  font-weight: 700;
  margin: 0;
}
.btn {
  display: flex !important;
  justify-content: center;
  align-items: center;
  span {
    min-width: auto;
  }
}
img {
  object-fit: cover;
}