.main {
  background-image: url("../img/main-bg.webp");
  background-repeat: no-repeat;
  background-position: right;
  background-size: cover;
  padding: 23.4375rem 0 9.375rem;
  &__inner {
    position: relative;
    z-index: 2;
  }
  &__wrap {
    display: flex;
    justify-content: space-between;
    align-items: flex-end;
    gap: 1.5rem;
  }
  &__left {
    max-width: 56.25rem;
    p {
      max-width: 43.75rem;
      font-family: Sansation;
      font-size: 2.25rem;
      line-height: 120%;
      font-weight: 300;
      margin: 2rem 0 0 0;
    }
  }
  h1 {
    font-size: 3rem;
    line-height: 120%;
    margin: 0;
    strong {
      font-size: 4.875rem;
    }
  }
  &__btn {
    width: 15.625rem;
    margin-top: 4rem;
  }
  &__price {
    position: relative;
    min-width: 15.625rem;
    min-height: 6.25rem;
    display: flex;
    justify-content: center;
    align-items: center;
    background-image: url("../img/price-bg.webp");
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    padding: 1rem;
    &:after {
      content: '';
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      width: calc(100% - 0.625rem);
      height: calc(100% - 0.625rem);
      background-image: url("../img/price-bg2.webp");
      background-repeat: no-repeat;
      background-position: center;
      background-size: cover;
    }
    p {
      position: relative;
      z-index: 1;
      font-size: 1.5rem;
      line-height: 130%;
      color: #000;
      strong {
        font-weight: 700;
      }
    }
  }
  &__cards {
    display: grid;
    grid-template-columns: repeat(4, calc(25%));
    margin-top: 6.25rem;
  }
  &-card {
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    background: rgba(0, 53, 46, 0.82);
    border: 0.3px solid rgba(196, 196, 196, 0.8);
    backdrop-filter: blur(3.5px);
    border-radius: 1.5rem;
    padding: 2rem;
    margin-left: -0.0625rem;
    &__text {
      font-size: 1.125rem;
      line-height: 120%;
      font-weight: 300;
      margin-bottom: auto;
      strong {
        font-size: 1.25rem;
        font-weight: 700;
      }
      span {
        font-size: 0.875rem;
        color: #B5B0B0;
        text-decoration: line-through;
      }
    }
    &__img {
      max-width: 16rem;
      margin-top: 1rem;
    }
  }
  &__img {
    position: absolute;
    top: 0;
    left: 0;
    width: 11.25rem;
    img {
      width: 100%;
    }
  }
  &__img2 {
    position: absolute;
    top: 15.625rem;
    left: 2rem;
    width: 6.625rem;
    img {
      width: 100%;
    }
  }
  &__img3 {
