.footer-top {
  background: rgb(0, 34, 29);
  padding: 5rem 0 1.875rem;
  &__inner {
    display: flex;
    justify-content: space-between;
    gap: 1.875rem;
  }
  &__left {
    ul {
      gap: 0.1875rem !important;
    }
  }
  &__logo {
    width: 15.625rem;
    display: block;
    margin-bottom: 1.5rem;
    img {
      width: 100%;
    }
  }
  ul {
    display: inline-grid;
    grid-template-columns: 100%;
    gap: 0.625rem;
    li {
      position: relative;
      font-size: 0.875rem;
      line-height: 130%;
      padding-left: 0.625rem;
    }
    li:after {
      content: '';
      position: absolute;
      top: 0.375rem;
      left: 0;
      width: 0.3125rem;
      height: 0.3125rem;
      background: #ffffff;
      border-radius: 50%;
    }
  }
  &__items {
    display: flex;
    justify-content: space-between;
    gap: 1.875rem;
  }
  &__subtitle {
    font-size: 1rem;
    line-height: 130%;
    font-weight: 700;
    margin-bottom: 1rem;
  }
  &__location {
    font-size: 0.875rem;
    line-height: 130%;
    font-weight: 300;
    span {
      display: block;
      color: rgb(23, 189, 166);
    }
  }
  &__time {
    font-size: 0.875rem;
    line-height: 130%;
    font-weight: 300;
    span {
      display: block;
      color: rgb(23, 189, 166);
    }
  }
  &__socials {
    margin-top: 1.5rem;
  }
  &__socials-title {
    font-size: 0.875rem;
    line-height: 130%;
    color: rgb(23, 189, 166);
    margin-bottom: 10px;
  }
  &__socials-wrap {
    display: flex;
    align-items: center;
    gap: 1.5rem;
  }
  &__social {
    position: relative;
    width: 2.5rem;
    height: 2.5rem;
    display: flex;
    justify-content: center;
    align-items: center;
    background: #077163;
    border-radius: 50%;
    overflow: hidden;
    &:hover {
      background: #39C2B0;
    }
    &:active {
      background: #00352E;
    }
    img {
      position: relative;
      z-index: 1;
      max-width: 1.5rem;
    }
    span {
      position: absolute;
      top: 0;
      left: 0;
    }
  }
  &__phone {
    font-family: Sansation;
    font-size: 24px;
    line-height: 130%;
    font-weight: 700;
    &:hover {
      color: #077163;
    }
  }
  &__btns {
    flex-shrink: 0;
    display: flex;
    flex-direction: column;
    gap: 0.75rem;
  }
  &__btn {
    justify-content: start !important;
    background: transparent;
    padding-left: 1.5rem;
    &:hover {
      span {
        color: #077163;
        border-color: #077163;
      }
    }
    span {
      position: relative;
      font-size: 1rem;
      line-height: 130%;
      color: #ffffff;
      border-bottom: 0.0625rem dashed #ffffff;
      padding: 0 0 0.1875rem 0;
      overflow: unset;
    }
    span:after {
      content: '';
      position: absolute;
      top: 50%;
      left: -1.25rem;
      transform: translate(0, -50%);
      width: 0.5rem;
      height: 0.5rem;
      background: #ffffff;
      border-radius: 50%;
    }
    span:before {
      content: '';
      position: absolute;
      top: 50%;
      left: -1.375rem;
      transform: translate(0, -50%);
      width: 0.75rem;
      height: 0.75rem;
      background: #17BDA6;
      filter: blur(2.8499999046325684px);
    }
  }
}
.footer-bottom {
  background: rgb(0, 53, 46);
  padding: 1.5rem 0;
  font-size: 0.875rem;
  line-height: 130%;
  &__inner {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
    gap: 1.875rem;
  }
  &__left {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    gap: 1.875rem;
  }
  &__logo {
    width: 11.875rem;
    display: block;
    img {
      width: 100%;
    }
  }
  &__info {
    a {
      color: rgb(23, 189, 166);
    }
  }
  &__text {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    gap: 0.625rem;
    p {
      margin: 0;
    }
  }
}

@media screen and (max-width: 1399px) {
  .footer-top {
    padding: 3.125rem 0;
    &__inner {
      flex-wrap: wrap;
    }
  }
}

@media screen and (max-width: 768px) {
  .footer-top {
    &__inner {
      flex-direction: column;
    }
    &__items {
      flex-direction: column;
    }
  }
}